/**
 * Layout component that queries for data
 * with Gatsby's useStaticQuery component
 *
 * See: https://www.gatsbyjs.org/docs/use-static-query/
 */

import React from "react"
import PropTypes from "prop-types"
import { useStaticQuery, graphql } from "gatsby"
import Img from "gatsby-image"
import "normalize.css"

import Footer from "../Footer"
import Header from "../Header"
import Hero from "../Hero"
import "../../theme/index.css"

import { content, wrapper } from "./Layout.module.css"

const socialLinks = [
  {
    title: "simon360 on GitHub",
    type: "github",
    url: "https://www.github.com/simon360",
  },
  {
    title: "simon360 on Instagram",
    type: "instagram",
    url: "https://www.instagram.com/simon360",
  },
  {
    title: "sadl-uk on LinkedIn",
    type: "linkedin",
    url: "https://www.linkedin.com/in/sadl-uk/",
  },
  {
    title: "@simon360 on Twitter",
    type: "twitter",
    url: "https://www.twitter.com/simon360",
  },
]

const Layout = ({ children, isHeroHeader }) => {
  const data = useStaticQuery(graphql`
    query SiteTitleQuery {
      file(relativePath: { eq: "hero-london-2.jpg" }) {
        childImageSharp {
          fluid(maxHeight: 1024, maxWidth: 2048) {
            ...GatsbyImageSharpFluid
          }
        }
      }
      site {
        siteMetadata {
          title
        }
      }
    }
  `)

  return (
    <div className={wrapper}>
      {/* Add a target for the Return To Top link */}
      {/* eslint-disable-next-line jsx-a11y/anchor-is-valid, jsx-a11y/anchor-is-valid, jsx-a11y/anchor-has-content */}
      <a id="top" />

      {isHeroHeader && (
        <Hero
          background={
            <Img
              fluid={data.file.childImageSharp.fluid}
              style={{ height: "100%" }}
            />
          }
        >
          <Header
            siteTitle={data.site.siteMetadata.title}
            socialLinks={socialLinks}
          />
        </Hero>
      )}

      {!isHeroHeader && (
        <Header
          socialLinks={socialLinks}
          siteTitle={data.site.siteMetadata.title}
        />
      )}

      <main className={content}>{children}</main>

      <Footer socialLinks={socialLinks} />
    </div>
  )
}

Layout.propTypes = {
  children: PropTypes.node.isRequired,
  isHeroHeader: PropTypes.bool.isRequired,
}

export default Layout
